@use '../util' as *;

.card-grid{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(2,auto); 
    gap: rem(23);  
    justify-items: start;
    
    .card__count {
        margin-bottom: 0;
    }
    .card__count, .card__followersChange {
        align-self: end;
    }
    img, .card__followersChange {
        justify-self: end;
    }

}
