html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--background);
  color: var(--text-color);
  min-height: 100vh;
}

/*Example: 
h1 {
    @include breakpoint(large) {
        font-size: 3rem;
    }
}

// Resulting CSS
@media (min-width: 87.5em) {
    h1 {
        font-size: 3rem;
    }
}
*/
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen:hsl(163, 72%, 41%);
  --brightred:hsl(356, 69%, 56%);
  --facebook:hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youTube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light-text: hsl(230, 19%, 60%);
  --toggle-light-bg: hsl(230, 22%, 74%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(222, 50%, 96%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

body.dark {
  --background:linear-gradient(
  to bottom,
  var(--dark-top-bg) 0%,
  var(--dark-top-bg) 25%,
  var(--dark-bg) 25%,
  var(--dark-bg) 100%
  );
  --header-background:var(--dark-top-bg);
  --text-color:var(--dark-text2);
  --subtitle-color:var(--dark-text1);
  --label-color:var(--dark-text1);
  --toggle-background: linear-gradient(225deg,var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  --toggle-button:var(--dark-card);
  --button-focus:var(--light-bg);
  --card-background:var(--dark-card);
  --card-text-color:var(--dark-text1);
  --card-hover:var(--dark-card-hover);
  --card-count:var(--light-bg);
}

body.light {
  --background:linear-gradient(
      to bottom,
      var(--light-top-bg) 0%,
      var(--light-top-bg) 25%,
      var(--light-bg) 25%,
      var(--light-bg) 100%
  );
  --header-background: var(--light-top-bg);
  --text-color:var(--light-text2);
  --subtitle-color:var(--light-text1);
  --label-color:var(--toggle-light-text);
  --toggle-background:var(--toggle-light-bg);
  --toggle-button:var(--light-top-bg);
  --button-focus:var(--dark-bg);
  --card-background:var(--light-card);
  --card-text-color:var(--light-text1);
  --card-hover:var(--light-card-hover);
  --card-count:var(--dark-bg);
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--subtitle-color);
}
@media (max-width: 639px) {
  .header {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    margin-top: 2.25rem;
  }
  .header__title {
    width: 100%;
  }
}
@media (min-width: 639px) {
  .header {
    display: flex;
    flex-wrap: wrap;
    margin-top: 2.25rem;
  }
}
@media (min-width: 639px) and (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 639px) {
  .header__title {
    width: 100%;
  }
}
@media (min-width: 639px) and (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
@media (min-width: 639px) and (min-width: 639px) {
  .header__hr {
    display: none;
  }
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--label-color);
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  height: 1.5rem;
  position: relative;
}
.toggle input[type=radio] {
  margin: 0 -0.125rem;
  opacity: 0;
  height: 1.5rem;
  width: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid var(--button-focus);
}
.toggle__background {
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  display: block;
  border-radius: 0.75rem;
  background: var(--toggle-background);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 300ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: 50%;
  transform: translate(-50%);
}

.cards {
  display: grid;
  gap: 1.875rem;
  grid-template-columns: 1fr;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  background: var(--card-background);
  color: var(--card-text-color);
  border-radius: 0.3125rem;
  text-align: center;
  padding: 1.5625rem;
  overflow: hidden;
  cursor: pointer;
  transition: background 150ms ease-in-out;
}
.card:hover {
  background: var(--card-hover);
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding: 1.875rem;
}
.card--instagram::before {
  content: "";
  display: block;
  height: 0.25rem;
  width: 100%;
  background: linear-gradient(255deg, var(--instagram-end), var(--instagram-middle), var(--instagram-start));
  position: absolute;
  top: 0;
  left: 0;
}
.card--youtube {
  border-top: 0.25rem solid var(--youTube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.25rem;
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__count {
  color: var(--card-count);
  font-weight: 700;
  letter-spacing: -0.125rem;
  margin-bottom: 0.25rem;
  line-height: 1;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__followersContainer {
  margin-bottom: 1.5625rem;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__followersChange {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followersChange--up {
  color: var(--limegreen);
}
.card__followersChange--down {
  color: var(--brightred);
}
.card__followersChange img {
  margin-right: 0.25rem;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 1.4375rem;
  justify-items: start;
}
.card-grid .card__count {
  margin-bottom: 0;
}
.card-grid .card__count, .card-grid .card__followersChange {
  align-self: end;
}
.card-grid img, .card-grid .card__followersChange {
  justify-self: end;
}